<template>
  <div>
    <admin-nav></admin-nav>
    <form class="container mb-5" id="form" @submit.stop.prevent="addToProductSubmit">
      <div class="form-row">
        <div class="form-group col-md-6">
          <label for="name">名稱</label>
          <input
            type="text"
            class="form-control"
            id="name"
            name="name"
            placeholder="商品名稱"
            v-model="product.name"
            required="required"
          />
        </div>
        <div class="form-group col-md-6">
          <label for="categoryId">分類</label>
          <select
            name="categoryId"
            id="categoryId"
            class="form-control"
            v-model="product.categoryId"
          >
            <option value disabled>請選擇</option>
            <option
              v-for="category in categories"
              :key="category.id"
              :value="category.id"
            >{{category.category}}</option>
          </select>
        </div>
      </div>
      <div class="form-row">
        <div class="form-group col-md-6">
          <label for="originPrice">原價</label>
          <input
            type="text"
            v-model="product.originPrice"
            name="originPrice"
            id="originPrice"
            class="form-control"
            placeholder="商品原價"
            required="required"
          />
        </div>
        <div class="form-group col-md-6">
          <label for="sellPrice">售價</label>
          <input
            type="text"
            name="sellPrice"
            id="sellPrice"
            class="form-control"
            placeholder="商品售價"
            v-model="product.sellPrice"
            required="required"
          />
        </div>
        <div class="form-group col-md-12">
          <label for="description">產品描述</label>
          <textarea
            name="description"
            id="description"
            class="form-control"
            cols="120"
            rows="3"
            placeholder="請輸入商品的簡介"
            v-model="product.description"
          ></textarea>
          <!-- <div class="form-group col-md-12">
            <input type="file" multiple="multiple" id="images" name="images" />
          </div>-->
        </div>
        <div class="form-group col-md-12">
          <div class="DashboardContainer" name="image"></div>
        </div>
      </div>
      <a href="#" class="btn btn-outline-success mx-3" @click="$router.go(-1)">回上一頁</a>
      <button type="submit" class="btn btn-outline-primary mx-3" :disabled="isProcessing">新增商品</button>
    </form>
  </div>
</template>
<script>
import "@uppy/dashboard/dist/style.min.css";
import Uppy from "@uppy/core";
import Dashboard from "@uppy/dashboard";
// import Url from "@uppy/url";
import adminNav from "./../components/adminNav";
import adminAPI from "./../apis/admin";
import { Toast } from "./../utils/helpers";
export default {
  components: {
    adminNav
  },
  data() {
    return {
      uppy: null,
      product: {
        id: -1,
        name: "",
        categoryId: "",
        originPrice: "",
        sellPrice: "",
        description: ""
      },
      images: [],
      categories: []
    };
  },
  mounted() {
    this.initUppy();
  },
  created() {
    this.fetchAdminCategories();
  },
  computed: {
    isProcessing() {
      return this.$store.state.isProcessing;
    }
  },
  methods: {
    openUppyDashboard() {
      this.uppy.getPlugin("Dashboard").openModal();
    },
    initUppy() {
      this.uppy = Uppy({
        autoProceed: false,
        debug: true,
        restrictions: {
          maxFileSize: 50 * 1024 * 1024,
          maxNumberOfFiles: 10,
          minNumberOfFiles: 1,
          allowedFileTypes: ["image/*"]
        },
        meta: {
          modelId: this.modelId,
          collection: this.collection
        },
        onBeforeFileAdded: () => {
          Promise.resolve();
        },
        onBeforeUpload: files => {
          this.files = files;
          Promise.resolve();
        }
      }).use(Dashboard, {
        trigger: ".UppyModalOpenerBtn",
        inline: true,
        target: ".DashboardContainer",
        replaceTargetContent: true,
        showProgressDetails: true,
        note: "Images and video only, 1–10 files",
        height: 470,
        metaFields: [
          { id: "name", name: "Name", placeholder: "file name" },
          {
            id: "caption",
            name: "Caption",
            placeholder: "describe what the image is about"
          }
        ],
        browserBackButtonClose: true
      });
      // .use(Url,{})
      // this.uppy.getPlugin("Url").addFile("data:image/png;base64,")

      this.uppy.on("complete", result => {
        this.files = result.successful;
      });
    },
    async addToProductSubmit(e) {
      try {
        if (
          !this.product.name ||
          !this.product.sellPrice ||
          !this.product.originPrice ||
          !this.product.description
        ) {
          Toast.fire({
            icon: "warning",
            title: "請確認輸入的內容"
          });
          return;
        }
        this.$store.dispatch("updateProcessing", true);
        this.images = this.uppy.getFiles().map(f => f.data);

        const form = e.target;
        const formData = new FormData(form);

        const { data, statusText } = await adminAPI.products.post({
          formData,
          images: this.images
        });

        if (statusText !== "OK" || data.status !== "success") {
          throw new Error(statusText);
        }
        this.$store.dispatch("updateProcessing", false);
        this.$router.push({ name: "adminProducts" });
      } catch (error) {
        this.$store.dispatch("updateProcessing", false);
        Toast.fire({
          icon: "error",
          title: "建立商品失敗"
        });
      }
    },
    async fetchAdminCategories() {
      try {
        const { data, statusText } = await adminAPI.categories.get();

        if (statusText !== "OK") {
          throw new Error(statusText);
        }

        this.categories = data.categories;
      } catch (error) {
        Toast.fire({
          icon: "error",
          title: "無法取得類別列表"
        });
      }
    }
  },
  beforeDestroy() {
    this.uppy.close();
  }
};
</script>